html {
    --main-red: rgb(222, 43, 43);
    --main-dark-red: rgb(157, 26, 26);
    --main-orange: rgb(243, 167, 26);
}

html, body, #root {
    width: 100%;
    height: 100%;
    margin: 0;
}

.App {
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header {
    width: 100%;
    padding-top: 20px;
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    border-bottom: 2px solid var(--main-red);
}

.logo {
    cursor: pointer;
}

.nav-btn {
    margin-right: 40px;
    width: 130px;
    height: 30px;
    background-color: var(--main-red);
    color: var(--main-orange);
    font-size: 1.1em;
    font-weight: bolder;
    border: 2px solid var(--main-orange);
    border-bottom: none;
}

.nav-btn:hover {
    border: 2px solid var(--main-red);
    border-bottom: none;
    color: var(--main-red);
    background-color: var(--main-orange);
    cursor: pointer;
}

.home {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    width: 70%;
}

.home-section:nth-child(2n) {
    background-color: var(--main-red);
    color: var(--main-orange);
}

.hero {
    display: flex;
    justify-content: center;
    align-items: center;
}

.hero-adopt {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 100px;
    color: var(--main-dark-red);
}

.home-learn {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.home-learn img {
    width: 60%;
    height: 80%;
    box-shadow: 0px 0px 4px 4px var(--main-orange);
}

.home-learn-text {
    display: flex;
    flex-direction: column;
    width: 30%;
    height: 80%;
    font-size: 1.2em;
}

.home-btn {
    width: 150px;
    height: 40px;
    background-color: var(--main-orange);
    color: var(--main-red);
    font-size: 1em;
    font-weight: bolder;
    border: 2px solid var(--main-dark-red);
}

.home-btn:hover {
    color: var(--main-orange);
    background-color: var(--main-red);
    cursor: pointer;
}

.about {
    width: 70%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    margin: 30px;
}

.about img {
    width: 40%;
    box-shadow: 0px 0px 3px 3px var(--main-dark-red);
}

.about-text {
    width: 40%;
    font-size: 1.2em;
}

.browse {
    width: 65%;
    max-height: 600px;
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid black;
    margin: 30px;
    background-color: var(--main-red);
    color: var(--main-orange);
    font-weight: bolder;
    padding: 50px;
    padding-top: 0px;
}

.browse-search {
    width: 90%;
    display: flex;
    justify-content: flex-end;
}

.browse-listings {
    width: 100%;
    display: grid;
    gap: 40px;
    grid-template-columns: repeat(2, 1fr);
}

.listing {
    display: flex;
    height: 150px;
    margin-bottom: 10px;
    margin-top: 10px;
    padding: 10px;
    background-color: var(--main-orange);
    color: var(--main-dark-red);
}

.listing:hover {
    background-color: white;
    cursor: pointer;
}

.listing-pic {
    width: 150px;
    display: flex;
    justify-content: center;
}

.listing-pic-img {
    height: 100%;
    max-width: 100%;
}

.listing-info {
    margin-left: 10px;
    height: 100%;
}

.listing-section {
    height: 25%;
}

.footer {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--main-red);
    border-top: 2px solid var(--main-orange);
    color: var(--main-orange);
    font-weight: bolder;
    margin-top: 20px;
}